.header {
  &-top {
    background: #6437D6;
    color: #ffffff;
    padding: 10px 0;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__left {
      display: flex;
      align-items: center;
    }
    &__right {
      display: flex;
      align-items: center;
    }
    &__logo {
      display: flex;
      align-items: center;
      &-img {
        width: 50px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      &-desc {
        margin-left: 9px;
        span:nth-child(1) {
          font-size: 15px;
          line-height: 120%;
          display: block;
        }
        span:nth-child(2) {
          font-size: 10px;
          line-height: 120%;
          opacity: 0.6;
          display: block;
        }
      }
    }
    &__info {
      font-size: 18px;
      line-height: 120%;
      margin-left: 15px;
      position: relative;
      span:nth-child(2) {
        text-transform: lowercase;
      }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -3px;
        width: 100%;
        height: 2px;
        background: #FFFFFF;
        opacity: 0.4;
      }
    }
    &__tel {
      width: 184px;
      height: 37px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFFFFF;
      border-radius: 60px;
      &-number {
        font-size: 17px;
        line-height: 120%;
        letter-spacing: 0.5px;
        color: #000000;
        margin-left: 7px;
      }
    }
    &__desc {
      font-size: 18px;
      line-height: 120%;
      margin: 0 7px;
    }
    &__btn {
      width: 142px;
      height: 38px;
      background: transparent;
      border-radius: 60px;
      color: #ffffff;
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.4;
        border: 2px solid #FFFFFF;
        border-radius: 60px;
      }
    }
  }
  &-bg {
    background: #FFFFFF;
    box-shadow: 0px 0px 60px rgba(148, 148, 148, 0.2);
  }
  &-center {
    padding: 15px 0;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -15px;
        width: 100%;
        height: 1px;
        background: #000000;
        opacity: 0.1;
      }
    }
    &__logo {
      font-weight: 700;
      font-size: 39px;
      line-height: 120%;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        background: linear-gradient(74deg, #627799 0%, #3A4A63 100%);
        border-radius: 60px;
        padding: 4px 10px;
        color: #FFFFFF;
        margin-top: -15px;
        margin-left: 10px;
      }
    }
    &__list {
      display: flex;
      align-items: center;
    }
    &__link {
      font-weight: 500;
      font-size: 15px;
      line-height: 120%;
      &:not(:first-child) {
        margin-left: 35px;
      }
      &2 {
        display: flex;
        align-items: center;
        span {
          font-weight: 600;
          font-size: 12px;
          line-height: 120%;
          background: linear-gradient(74deg, #627799 0%, #3A4A63 100%);
          border-radius: 10px;
          padding: 5px 10px;
          margin-left: 10px;
          color: #FFFFFF;
        }
      }
    }
    &__item {
      position: relative;
      &--active {
        .header-center__item-hidden {
          opacity: 1;
          pointer-events: unset;
        }
      }
      &-show {
        font-weight: 500;
        font-size: 15px;
        line-height: 120%;
        display: flex;
        align-items: center;
        cursor: default;
        transition: all 0.2s;
        position: relative;
        z-index: 3;
        i {
          margin-top: 5px;
          margin-left: 9px;
        }
      }
      &-hidden {
       position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        opacity: 0;
        pointer-events: none;
        padding-top: 30px;
      }
      &-inner {
        background: #FFFFFF;
        box-shadow: 0px 0px 60px rgba(148, 148, 148, 0.2);
        padding: 15px;
        min-width: 150px;
        a {
          font-weight: 500;
          font-size: 15px;
          line-height: 120%;
          display: block;
          &:not(:first-child) {
            margin-top: 7px;
          }
        }
      }
    }
    &__menu {
      font-size: 13px;
      line-height: 120%;
      display: flex;
      align-items: center;
      display: none;
    }
    &__burger {
      position: relative;
      z-index: 5;
      width: 46px;
      height: 34px;
      background: linear-gradient(74deg, #627799 0%, #3A4A63 100%);
      border-radius: 5px;
      padding: 5px 10px;
      margin-left: 10px;
      cursor: pointer;
      &--active {
        span {
          top: 50% !important;
          left: 50% !important;
          bottom: unset !important;
          &:nth-child(1) {
            transform: translate(-50%, -50%) rotate(-45deg);
          }
          &:nth-child(2) {
            display: none;
          }
          &:nth-child(3) {
            transform: translate(-50%, -50%) rotate(45deg);
          }
        }
      }
      span {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        width: 26px;
        height: 2.5px;
        background: #ffffff;
        transition: all 0.5s;
        &:nth-child(1) {
          top: 7px;
        }
        &:nth-child(2) {
          top: 50%;
          transform: translate(-50%, -50%);
        }
        &:nth-child(3) {
          bottom: 7px;
        }
      }
    }
  }
  &-bottom {
    &__nav {
      width: 100%;
      height: 100%;
      overflow-x: auto;
      padding: 20px 0;
      &::-webkit-scrollbar {
        width: 100%;
        height: 0px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: transparent;
        border-radius: 3px;
      }
    }
    &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__link {
      font-size: 15px;
      line-height: 120%;
      color: rgba(0, 0, 0, 0.6);;
      position: relative;
      flex-shrink: 0;
      &--active {
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -20px;
          width: 100%;
          height: 2px;
          background: #000000;
          opacity: 0.35;
        }

      }
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .header {
    &-top {
      &__info {
        margin-left: 30px;
        span:nth-child(1) {
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &-top {
      &__info {
        font-size: 14px;
      }
      &__tel {
        width: 154px;
        height: 31px;
        &-number {
          font-size: 14px;
        }
      }
      &__desc {
        font-size: 14px;
      }
      &__btn {
        height: 31px;
        font-size: 14px;
      }
    }
    &-center {
      &__logo {
        font-size: 30px;
        span {
          font-size: 12px;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .header {
    &-top {
      &__logo {
        display: none;
      }
      &__info {
        margin-left: 0;
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          text-transform: unset;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &-top {
      &__desc {
        display: none;
      }
      &__btn {
        display: none;
      }
    }
    &-center {
      &__nav {
        position: fixed;
        z-index: 4;
        top: 0;
        left: -120%;
        width: 100%;
        height: 100vh;
        background: #FFFFFF;
        box-shadow: 0px 0px 30px rgba(99, 99, 99, 0.1);
        padding: 120px 15px 30px;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
      }
      &__list {
        justify-content: center;
        flex-direction: column;
      }
      &__item {
        &--active {
          .header-center__item-hidden {
            display: block;
          }
        }
        &-show {
          justify-content: center;
        }
        &-hidden {
          position: static;
          padding-top: 10px;
          opacity: 1;
          pointer-events: all;
          display: none;
        }
        &-inner {
          padding: 0 0 0 15px;
          background: transparent;
          box-shadow: unset;
        }
      }
      &__link {
        &:not(:first-child) {
          margin: 10px 0 0 0;
        }
      }
      &__menu {
        display: flex;
      }
    }
    &-bottom {
      &__link {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .header {
    &-top {
      .container {
        padding: 0 5px;
      }
    }
  }
}

@media screen and (max-width: 379px) {
  .header {
    &-top {
      .container {
        padding: 0 15px;
      }
      &__info {
        &:after {
          display: none;
        }
      }
      &__right {
        margin-left: 15px;
      }
    }
  }
}