.main {
  padding: 40px 0 60px;
  &__inner {
    text-align: center;
  }
  &__title {
    font-size: 45px;
    line-height: 120%;
    font-weight: 400;
    letter-spacing: 0.2px;
  }
  &__desc {
    font-size: 19px;
    line-height: 120%;
    max-width: 600px;
    margin: 15px auto 0;
    span {
      background: #ece0ad;
      border-radius: 5px;
      padding: 0 5px;
    }
  }
  .video--active {
   iframe {
     position: relative;
     z-index: 3;
   }
  }
  &__video {
    max-width: 630px;
    width: 100%;
    height: 360px;
    margin: 18px auto 0;
    background-image: url("../img/video-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    position: relative;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(74deg, #627799 0%, rgba(58, 74, 99, 0.1) 98%);
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 130px;
      height: 130px;
      background: linear-gradient(177deg, rgba(255, 214, 0, 0.1) 0%, rgba(255, 193, 30, 0.6) 100%);
      border-radius: 50%;
    }
    iframe {
      width: 100%;
      height: 100%;
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: transparent;
      padding: 0;
    }
    &-play {
      width: 80px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFFFFF;
      border-radius: 50%;
      i {
        width: 50px;
        img {
          width: 100%;
        }
      }
    }
    &-desc {
      position: absolute;
      z-index: 2;
      left: 40px;
      bottom: 60px;
      max-width: 240px;
    }
    &-title {
      display: inline-block;
      span:nth-child(1) {
        background: #FFFFFF;
        border-radius: 10px 10px 0 0;
        padding: 5px 12px 0 12px;
        display: table;
        align-items: start;
        font-size: 25px;
        line-height: 120%;
      }
      span:nth-child(2) {
        background: #FFFFFF;
        border-radius: 0 10px 10px 0;
        padding: 0 12px 5px 12px;
        display: inline-block;
        align-items: start;
        font-size: 25px;
        line-height: 120%;
      }
    }
    &-text {
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
      text-align: start;
      color: #FFFFFF;
      opacity: 0.75;
      margin-top: 10px;
    }
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
  }
  &__tel {
    width: 311px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(105deg, #FFD600 0%, #FFC11E 100%);
    border-radius: 60px;
    span {
      font-size: 20px;
      line-height: 120%;
      letter-spacing: 0.5px;
      margin-left: 12px;
    }
  }
  &__btn {
    width: 311px;
    height: 90px;
    font-size: 18px;
    line-height: 120%;
    color: #474747;
    margin-left: 10px;
    background: #FCFCFC;
    border: 1px solid #BFBFBF;
    border-radius: 60px;
    position: relative;
    span {
      position: absolute;
      top: -12px;
      right: 0;
      font-size: 12px;
      line-height: 120%;
      background: linear-gradient(74deg, #627799 0%, #3A4A63 100%);
      border-radius: 30px;
      color: #ffffff;
      padding: 5px 10px;
    }
  }
  &__info {
    font-size: 14px;
    line-height: 120%;
    margin-top: 15px;
    color: rgba(0,0,0,.6);
    display: inline-block;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -26px;
      transform: translate(0, -50%);
      width: 20px;
      height: 20px;
      background: #12C75A;
      opacity: 0.25;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -21px;
      transform: translate(0, -50%);
      width: 10px;
      height: 10px;
      background: #12C75A;
      border-radius: 50%;
    }
  }
}
.advantages {
  padding-bottom: 70px;
  &__inner {
    padding: 30px 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #000000;
      opacity: 0.1;
    }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: #000000;
      opacity: 0.1;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-left: 75px;
    }
    &-icon {
      flex-shrink: 0;
      width: 40px;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 15px;
    }
    &-title {
      font-weight: 500;
      font-size: 25px;
      line-height: 120%;
    }
    &-text {
      font-size: 15px;
      line-height: 120%;
      color: rgba(0,0,0,.6);
      margin-top: 10px;
    }
  }
}
.modal {
  background-color: rgba(0, 0, 0, 0.88);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  &__dialog{
    max-width: 1000px;
    width: 100%;
    position: relative;
    margin: 0 auto;
    padding: 0 15px;
    @media screen and (max-width: 992px) {
      margin: 120px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__video {
    video {
      width: 100%;
      height: 100%;
    }
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__close{
    position: absolute;
    top: -60px;
    right: 15px;
    background: transparent;
    padding: 0;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    i {
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: gray;
      border-radius: 50%;
      margin-left: 10px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .advantages {
    &__item {
      &:not(:first-child) {
        margin-left: 50px;
      }
    }
  }
  .modal {
    &__dialog {
      max-width: 900px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 40px 0;
    &__title {
      font-size: 24px;
    }
    &__desc {
      font-size: 14px;
      max-width: 340px;
    }
    &__video {
      height: 300px;
      &:before {
        left: unset;
        right: 49px;
        transform: translate(0, -50%);
        width: 100px;
        height: 100px;
      }
      &-btn {
        left: unset;
        right: 69px;
        transform: translate(0, -50%);
      }
      &-play {
        width: 60px;
        height: 60px;
        i {
          width: 35px;
        }
      }
      &-desc {
        left: 48px;
        bottom: 40px;
        max-width: 180px;
      }
      &-title {
        span:nth-child(1) {
          font-size: 18px;
        }
        span:nth-child(2) {
          font-size: 18px;
        }
      }
      &-text {
        font-size: 14px;
        margin-top: 6px;
      }
    }
    &__tel {
      width: 177px;
      height: 57px;
      span {
        font-size: 14px;
        margin-left: 6px;
      }
    }
    &__btn {
      width: 177px;
      height: 57px;
      font-size: 14px;
      margin-left: 6px;
      span {
        top: -9px;
        font-size: 10px;
        padding: 3px 7px;
      }
    }
    &__info {
      font-size: 12px;
    }
  }
  .advantages {
    &__items {
      max-width: 500px;
      margin: 0 auto;
      display: block;
    }
    &__item {
      &:not(:first-child) {
        margin: 25px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .advantages {
    padding-bottom: 15px;
    &__items {
      max-width: 370px;
    }
    &__item {
      &-icon {
        width: 30px;
      }
      &-desc {
        margin-left: 10px;
      }
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 12px;
        margin-top: 7px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages {
    &__items {
      max-width: 270px;
    }
  }
}

@media screen and (max-width: 379px) {
  .main {
    &__video {
      &:before {
        left: 50%;
        right: unset;
        transform: translate(-50%, -50%);
      }
    }
    &__video-btn {
      left: 50%;
      right: unset;
      transform: translate(-50%, -50%);
    }
    &__video-desc {
     display: none;
    }
    &__btns {
      flex-direction: column;
    }
    &__tel {
      width: 100%;
    }
    &__btn {
      width: 100%;
      margin: 15px 0 0 0;
    }
  }
}